import React from 'react';

const Image = props=>{
    let styles = {
        width: '100%',
        height:'300px',
        objectfit: 'cover',
      };
     
    return (
       
    <div>
    <img src={props.url} style={styles}  alt='Doggo'></img>
    </div>

    )
}

export default Image;