import React, {Component} from 'react';
import Image from './components/Image';
import Form from './components/form';
import './styles.css';

class App extends Component {

   state={
     url: '',
  }

componentDidMount(){
      const API_URL="https://dog.ceo/api/breeds/image/random";
        fetch(API_URL).then(res => res.json())
        .then(
          (result) => {
        this.setState({
          url:result.message,
        });
    })
}
  dogApi= async e=>{
    e.preventDefault();
    //invocar el value del select
    var selected_breed = document.getElementById("dog_breeds").value;
    var dogURL=selected_breed.replace(/-/g,'/');
    const API_URL=`https://dog.ceo/api/breed/${dogURL}/images/random`;
    const respuesta= await  fetch(API_URL);
    const data= await respuesta.json();
    console.log(data.massage);
    this.setState({
      url:data.message,
    });
  };

render(){

  return (
    <div className='container p-5'>
      <div className='row'>
        <div className='col-sm-5 col-md-6  mx-auto'>
          <div className='card card-body rounded'>
            <h3 className='card-header text-center fondo'>Your Daily Dogoo App!</h3>
            <hr></hr>
            <Image {...this.state}/>
            <hr></hr>
            <Form dogApi={this.dogApi}/>
          </div>
        </div>
      </div>
    </div>
    );
  
}
 
}

export default App;
